@media only screen and (max-width: 767px) { .posts__img-wrap a { left: 50%; transform: translateX(-50%) rotate(-45deg); } }
.posts__img-wrap a:after { display: block; content: ""; background-image: url("../img/icons.png"); width: 30px; height: 30px; background-position: -274px -68px; position: absolute; left: 16px; top: 14px; z-index: 1; transform: rotate(45deg); }
.posts__item-inner { padding: 20px 15px; background: white; text-align: left; }
.posts__item-inner h5 { font-size: 30px; color: #777777; margin-bottom: 10px; font-weight: 400; }
@media only screen and (max-width: 767px) { .posts__item-inner h5 { font-size: 25px; } }
.posts__item-inner p { font-size: 22px; min-height: 130px; line-height: 1.2; }
.posts__item-footer { margin-top: 30px; }
@media only screen and (max-width: 767px) { .posts__item-footer { margin-top: 10px; } }
.posts__item-footer time, .posts__item-footer span { display: inline-block; font-size: 20px; color: #777777; vertical-align: middle; margin-right: 30px; }
.posts__item-footer time:before, .posts__item-footer time .posts__item-footer span:before, .posts__item-footer span:before, .posts__item-footer span .posts__item-footer span:before { display: inline-block; content: ""; background-image: url("../img/icons.png"); width: 32px; height: 32px; background-position: -127px -189px; vertical-align: middle; margin-right: 10px; }
.posts__item-footer span { margin-right: 0; }
.posts__item-footer span:before { background-image: url("../img/icons.png"); width: 32px; height: 29px; background-position: -235px -189px; }

.skills { margin: 80px 0 100px 0; display: flex; justify-content: space-between; align-items: stretch; flex-wrap: wrap; }
@media only screen and (max-width: 1024px) { .skills { margin: 70px 0; justify-content: space-around; } }
@media only screen and (max-width: 767px) { .skills { margin: 50px 0 0 0; } }
.skills__item { transform: scale(1); }
@media only screen and (max-width: 767px) { .skills__item { margin: 10px; } }
.skills__item.is-hidden { transform: scale(0); opacity: 0; }
.skills__item:nth-of-type(odd) .skills__circle { border-color: #777777; }
.skills__circle { width: 180px; height: 180px; text-align: center; border-radius: 50%; border: 7px solid #E83A93; padding-top: 55px; margin-bottom: 15px; }
.skills__circle:nth-of-type(even) { border-color: #777777; }
@media only screen and (max-width: 1024px) { .skills__circle { width: 150px; height: 150px; padding-top: 40px; margin: 0 auto; } }
@media only screen and (max-width: 767px) { .skills__circle { width: 100px; height: 100px; padding-top: 25px; } }
.skills__increase { font-size: 45px; color: #E83A93; }
@media only screen and (max-width: 1024px) { .skills__increase { display: block; } }
@media only screen and (max-width: 767px) { .skills__increase { font-size: 30px; } }

.projects { display: flex; justify-content: space-between; align-items: stretch; flex-wrap: wrap; margin: 80px 0; }
@media only screen and (max-width: 1024px) { .projects { margin: 70px 0; } }
@media only screen and (max-width: 767px) { .projects { margin: 50px 0 0 0; } }
.projects__item { overflow: hidden; position: relative; width: calc(100%/6); }
@media only screen and (max-width: 1024px) { .projects__item { width: calc(100%/4); } }
@media only screen and (max-width: 1024px) { .projects__item { width: calc(100%/3); } }
.projects__item:hover .projects__overlay { transform: scale(1) rotate(0deg); }
.projects__overlay { transition: 0.7s; position: absolute; left: 0; top: 0; background: rgba(232, 58, 147, 0.6); width: 100%; height: 100%; transform: scale(0) rotate(-350deg); }
.projects__overlay span { color: white; font-weight: bold; font-size: 30px; position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); }
@media only screen and (max-width: 1024px) { .projects__overlay span { font-size: 25px; } }
@media only screen and (max-width: 767px) { .projects__overlay span { font-size: 20px; } }
.projects img { max-width: 100%; height: auto; display: block; }

.offers { display: flex; justify-content: space-between; align-items: stretch; flex-wrap: wrap; margin: 80px 0; }
@media only screen and (max-width: 1024px) { .offers { margin: 70px 0; } }
@media only screen and (max-width: 767px) { .offers { margin: 50px 0; } }
.offers__item { width: 31%; background: white; padding: 30px 25px 100px 25px; color: #777777; position: relative; }
@media only screen and (max-width: 1024px) { .offers__item { padding: 25px 20px 90px 20px; } }
@media only screen and (max-width: 767px) { .offers__item { width: 100%; margin-top: 45px; padding: 15px 15px 60px 15px; } }
@media only screen and (max-width: 767px) { .offers__item:first-of-type { margin-top: 0; } }
.offers__item:hover h4, .offers__item:hover b, .offers__item.is-active h4, .offers__item.is-active b { color: #E83A93; }
.offers__item:hover .offers__btn, .offers__item.is-active .offers__btn { background: #E83A93; }
.offers__item h4 { margin: 0; font-size: 30px; font-weight: normal; }
.offers__item b { font-size: 50px; display: block; margin: 40px 0; }
@media only screen and (max-width: 1024px) { .offers__item b { font-size: 45px; margin: 45px 0; } }
@media only screen and (max-width: 767px) { .offers__item b { font-size: 35px; margin: 25px 0; } }
.offers__item p { font-size: 20px; border-bottom: 1px solid rgba(119, 119, 119, 0.4); padding-bottom: 5px; }
.offers__item p:last-of-type { border-bottom: none; }
.offers__btn { padding: 10px 20px; position: absolute; left: 50%; bottom: 0; transform: translate(-50%, 50%); text-transform: uppercase; display: inline-block; width: 200px; font-size: 25px; background: #777777; color: white; }

.subscribe { height: 600px; background: url("../img/subscribe.jpg") center center; background-size: cover; }
@media only screen and (max-width: 1024px) { .subscribe { height: 500px; } }
@media only screen and (max-width: 767px) { .subscribe { height: auto; } }
.subscribe h2, .subscribe p { color: white; }
.subscribe form { margin-top: 80px; }
@media only screen and (max-width: 1024px) { .subscribe form { margin-top: 70px; } }
@media only screen and (max-width: 767px) { .subscribe form { margin-top: 50px; } }
.subscribe form input[type="email"] { width: 800px; display: block; margin: 0 auto 70px auto; height: 80px; line-height: 80px; color: white; font-size: 25px; background: rgba(211, 211, 211, 0.5); padding: 0 30px; }
