.feed { margin-top: 80px; }
@media only screen and (max-width: 1024px) { .feed { margin-top: 70px; } }
@media only screen and (max-width: 767px) { .feed { margin-top: 50px; } }
.feed__box { display: flex; justify-content: space-between; align-items: stretch; flex-wrap: wrap; margin-bottom: 80px; }
@media only screen and (max-width: 1024px) { .feed__box { margin-bottom: 70px; } }
@media only screen and (max-width: 767px) { .feed__box { margin-bottom: 50px; } }
.feed__left { width: 49%; }
@media only screen and (max-width: 767px) { .feed__left { width: 100%; margin-bottom: 20px; } }
.feed__right { width: 49%; position: relative; }
.feed__right:after { display: block; content: "*"; color: #E83A93; position: absolute; right: 20px; top: 20px; font-size: 25px; }
@media only screen and (max-width: 767px) { .feed__right { width: 100%; } }
.feed__right textarea { display: block; width: 100%; height: 100%; resize: none; background: #eeeeee; border: none; padding: 20px 30px 20px 20px; font-size: 30px; color: #777777; }
