.footer__fb { display: block; background-image: url("../img/icons.png"); width: 30px; height: 64px; background-position: -274px 0px; }
.footer__tw { display: block; background-image: url("../img/icons.png"); width: 64px; height: 52px; background-position: 0px -133px; }
.footer__dr { display: block; background-image: url("../img/icons.png"); width: 64px; height: 64px; background-position: 0px 0px; }
